import React, { Suspense, lazy } from 'react'
import { isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
const MediaIcon = lazy(() => import('../../Elements/MediaIcon/MediaIcon'));
const Video = lazy(() => import('../../Elements/Media/Video'));
const Image = lazy(() => import('../../Elements/Media/Image'));

const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlayTagEmbed = isTagEmbed && (itemData.type === 3 || itemData.type === 5) && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed || isTagbox ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : 80 : isTagShop ? false : 100
  const mediaIcon = isTagShop && itemData.hotspot ? false : true

  return <div className="tb_cp_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
    {!autoPlayTagEmbed ? 
      <Suspense fallback={<></>}>
        <MediaIcon itemData={itemData} IconClass="tb_cp_media_icon" show={mediaIcon} isCenter={isTagEmbed} />
      </Suspense> 
    : null}
    {autoPlay || autoPlayTagEmbed ?
      <Suspense fallback={<></>}>
        <Video VideoClass="tb_cp_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={true} muted={true} /> 
      </Suspense>
      :
      <Suspense fallback={<></>}>
        <Image ImageClass="tb_cp_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} hotspot={true} />
      </Suspense>
    }
  </div>
}
export default Media